/* Fonts */
:root {
    --black-firs-color: #000000;
    --black-second-color: #050505;
    --whiit-first-color: #ffffff;
    --white-second-color: #f8f8f8;
    --grey-color: #b4b4b4;
    --breen-color: #9bb537;
}

html {
    scroll-behavior: smooth;
}

body {
    background-color: #f8f8f8;
    font-family: 'Inter', sans-serif;
    font-style: normal;
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    border: 0;
    clip: rect(0 0 0 0);
}

.container {
    max-width: 375px;
    padding-left: 20px;
    padding-right: 20px;
    margin-left: auto;
    margin-right: auto;
}

@media screen and (min-width: 768px) {
    .container {
        max-width: 768px;
        padding-left: 32px;
        padding-right: 32px;
    }
}

@media screen and (min-width: 1280px) {
    .container {
        max-width: 1280px;
        padding-left: 100px;
        padding-right: 100px;
    }
}