h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
    padding: 0;
}

ul,
ol {
    margin: 0;
    padding: 0;
    list-style: none;
}

a {
    font-family: inherit;
    color: currentColor;
    text-decoration: none;
}

button {
    display: block;
    padding: 0;

    font-family: inherit;
    cursor: pointer;
    border: none;
    outline: none;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

textarea {
    resize: none;
}